@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');
:root{
--font: 'Poppins', sans-serif;
--p_color: #183B3F;
--b_color: #000000;
--y_color: #ffaa00;
--text_white: #ffffff;
--text_gray: #666666;
--body_font: 16px;
--container_size: 1140px;
--m_container_size: 540px;
--t_container_size: 720px;
--sl_container_size: 960px;
}
*{
-webkit-box-sizing:border-box;
box-sizing:border-box;
}

body{
padding:0px;
margin:0px;
font-size:var(--body_font);
line-height:normal;
color:var(--b_color);
font-family: var(--font);
}
.container{
max-width:var(--container_size);
width:100%;
padding:0 15px;
margin:0 auto;
}
.row{
display:-webkit-box;
display:-ms-flexbox;
display:flex;
-ms-flex-wrap:wrap;
flex-wrap:wrap;
margin:0 -15px;
}
a, a:hover{color:var(--p_color);text-decoration:none;}
.p_color{color:var(--p_color)}
.s_color{color:var(--s_color)}
.text_white{color:var(--text_white)}
.text_gray{color:var(--text_gray)}
.bg_light{background:#f2f2f2;}
.text-center{text-align:center}
.text-left{text-align:left}
.text-right{text-align:right}
/**************/
.content_wraper{
display: flex;
flex-direction: column;
justify-content: space-between;
min-height: 100vh;
}
.body_content{
padding:50px 0px;
}
.logo{
padding: 10px 0;
box-shadow: 0 0 15px 0 rgba(0,0,0,0.06);
background:#fff;
}
.logo img{
width:100%;
max-width:170px;
}
footer{
padding: 15px 0;
background: #f9f9f9;
font-size: 13px;
font-weight: 400;
color: #666;
}
.exam_title h1{
font-size: 24px;
font-weight: 600;
margin: 0 0 30px;
color: #333;
border-left: 4px solid #ffaa00;
padding-left: 5px;
line-height: 1.2em;
}
.exam_time{
background: #f2f2f2;
padding: 10px;
margin: 0 -5px -5px;
border-radius: 0 0 4px 4px;
text-align: center;
font-size: 14px;
font-weight: 600;
}
.exam_time span{
color:#ea2c2c;
}
.exam_qustions{
display: flex;
gap: 0px 20px;
}
.qus_nums{
font-weight: 700;
color: #333;
width:80px;
flex: 0 0 80px;
}
.qustion_mcqs ul{
margin: 20px 0 0;
padding: 0px;
}
.qustion_mcqs ul li{
font-size: 15px;
margin: 0 0 10px;
padding-left: 10px;
}
.qustion_mcqs ul li:last-child{
margin:0px;
}
.qustion_mcqs ul li label{
position:relative;
padding-left: 24px;
display:table-cell;
line-height: 1.3em;
}
.qustion_mcqs ul li input{
display:none;
}
.qustion_mcqs ul li label:before,
.qustion_mcqs ul li label:after{
content: "";
width: 12px;
height: 12px;
position: absolute;
background: #fff;
left: 0;
top: 3px;
border-radius: 50%;
border: 2px solid var(--p_color);
}
.qustion_mcqs ul li label:after{
width: 8px;
height: 8px;
left: 4px;
top: 7px;
border: 0px;
background: var(--p_color);
display:none;
}
.qustion_mcqs ul li input:checked + label:after{
display:block;
}
.exam_qustions_btns a, .exam_start_cta a{
display: inline-block;
padding: 8px 20px;
background: var(--y_color);
border-radius: 3px;
font-size: 14px;
font-weight: 500;
}
.btn{
display: inline-block;
padding: 8px 20px;
background: var(--y_color);
border-radius: 3px;
font-size: 14px;
font-weight: 500;
margin: 2px;
border:1px solid var(--y_color);
cursor:pointer;
}
button.btn:hover,
a.btn:hover{
border-color:var(--y_color);
color:var(--y_color);
}
.exam_qustions_btns{
margin-top:80px;
border-top: 1px solid #f6f6f6;
padding-top: 10px;
}
.otp_wraper{
height: 100vh;
display: flex;
justify-content: center;
align-items: center;
padding:0 15px;
}
.otp_form{
max-width: 500px;
flex: 0 0 auto;
width: 100%;
background: #fff;
padding: 20px;
border-radius: 3px;
box-shadow: 0 0 50px 0 rgba(0,0,0,0.1);
}
.otp_content h3{
font-size: 16px;
font-weight: 500;
margin: 0 0 5px;
color: var(--p_color);
line-height: 1.3em;
}
.otp_content input{
width: 100%;
height: 50px;
font-family: inherit;
padding: 10px 20px;
-webkit-appearance: none;
border: 2px solid var(--p_color);
border-radius: 4px;
font-size: 16px;
color:inherit;
margin: 0 0 10px;
}
.otp_content input:focus{
outline:none;
}
.otp_content input[type="submit"]{
border: 0;
background: var(--y_color);
color: var(--p_color);
font-weight: 500;
text-transform: uppercase;
letter-spacing: 1px;
cursor: pointer;
margin: 0;
}
.otp_content p{
font-size: 14px;
margin: 0 0 15px;
color: #666;
}
.otp_resend{
border-top: 1px solid #ddd;
margin-top: 20px;
padding-top: 20px;
}
.otp_resend p{
font-size: 14px;
color: var(--p_color);
margin: 0 0 10px;
}
.otp_resend a{
font-size: 14px;
color: var(--p_color);
text-decoration:underline;
}
.logo_otp{
border-bottom: 1px solid #ddd;
padding-bottom: 10px;
margin: 0 0 20px;
}
.exam_start{
min-height: calc(100vh - 220px);
max-width: 700px;
margin: auto;
width: 100%;
background:#fff;
padding:20px;
display: flex;
flex-direction: column;
justify-content: space-between;
}
.exam_start_cta{
border-top: 1px solid #ddd;
padding-top: 20px;
margin: 20px 0 0;
}
.exam_guid h1, .exam_guid h2,
.exam_guid h3, .exam_guid h4,
.exam_guid h5, .exam_guid h6{
font-size: 20px;
margin: 0 0 10px;
color: var(--p_color);
font-weight: 600;
line-height: 1.3em;
}
.exam_guid p{
font-size: 15px;
margin: 0 0 15px;
line-height: 1.4em;
color: #666;
}
.exam_guid li{
font-size: 15px;
line-height: 1.4em;
color: #666;
}
.exam_guid li:not(:last-child){
margin:0 0 7px;
}
.exam_guid ol, .exam_guid ul{
margin: 0 0 15px;
}
.exam_guid{
padding-top:15px;
}
.exam_name{
font-size: 20px;
margin: 0 0 20px;
line-height: 1.3em;
font-weight: 500;
border-bottom: 1px solid #f6f6f6;
padding-bottom: 10px;
}
/*.exam_score_details{
display: flex;
justify-content: space-between;
gap: 0 20px;
align-items: center;
}*/
.exam_score_details > .candidate_dt{flex:3;}
.exam_score_details > .result_box{flex:1;}
.candidate_dt > span{
margin: 0 0 10px;
font-size: 14px;
color: #333;
display: flex;
}
.candidate_dt > span > span{
	flex:2;
}
.candidate_dt > span strong{
font-weight:600;
flex:1;
}
.result_box{
border: 1px solid #ddd;
padding: 10px;
border-radius: 5px;
font-size:14px;
color:#999;
margin-top: 74px;
}
.reult_score{
font-size: 40px;
line-height: 45px;
font-family: Times New Roman;
color:var(--p_color);
font-weight: bold;
}
.gd_bad{
display: block;
padding: 5px 10px;
background: #2abf2a;
color: #fff;
font-size: 16px;
text-align: center;
border-radius: 5px;
}
.gd_bag{
  display: block;
  padding: 5px 10px;
  background: red;
  color: #fff;
  font-size: 16px;
  text-align: center;
  border-radius: 5px;
  }
.exam_start.exam_result{
justify-content: unset;
}
.exam_score_dt{
margin:20px 0 0;
}
.cat-blog{
align-items: left !important;
padding: 4px 4px;
}
label.ans-label {
display: block;
margin: 15px 0px;
padding-left: 30px;
}
.total_row {
background: #f9f9f9;
}
.txtAlnCtr{
text-align: right;
padding: 4px 4px;
}
/**************/
.exam_cats{
  border-bottom: 1px solid #f6f6f6;
  margin-bottom: 20px;
  padding-bottom: 5px;
  }
