  .exam_cats span:not(:last-child):after{
  content:"\f054";
  font-family:"Font Awesome 6 Free";
  font-weight: 600;
  font-size: 14px;
  margin-left: 15px;
  }
  .exam_cats span{
  margin-right:15px;
  text-transform: capitalize;
  color:#cacaca;
  }
  .exam_cats span.active_cat{
  color:#000;
  }
.qustion{
font-size: 16px;
font-weight: 500;
margin-top: 5px;
}
.qus_nums span{
padding: 5px;
display: block;
border-radius:4px;
font-size: 14px;
text-align: center;
background:#333;
color:#fff;
}
.ans-label{
position:relative;
}
.ans-label input{
display:none;
}
.ans-label span:before,
.ans-label span:after{
content: "";
width: 20px;
height: 20px;
position: absolute;
left: 0;
top: 3px;
border-radius: 4px;
border: 2px solid #333;
z-index:1;
}
.ans-label input:checked + span:after{
content:"\2713";
background:#05bd21;
color:#fff;
text-align:center;
line-height:17px;
font-size: 13px;
font-weight: bold;
border-color:#05bd21;
}
.video-inner{
border: 1px solid #f6f6f6;
padding: 5px;
border-radius: 5px;
box-shadow: 0 0 10px 0 rgba(0,0,0,0.05);
}
.video_section{
position: sticky;
top: 20px;
}
.qustions_result_table{
border-radius: 5px;
overflow: hidden;
margin-top: 20px;
}
.qustions_result_table table td,
.qustions_result_table table th{
border: 1px solid #f2f2f2;
padding: 5px;
font-size: 14px;
color: #333;
}
.qustions_result_table table th{
font-weight: 600;
text-transform: capitalize;
background:#f9f9f9;
}
.qustions_result_table table th:not(:first-child),
.qustions_result_table table td:not(:first-child){
text-align:center;
}
.spinner_outer{
height: 100vh;
width: 100%;
display: flex;
justify-content: center;
align-items: center;
}

@media(max-width:1199px){
.container{max-width:var(--sl_container_size);}
}
@media(max-width:991px){
.container{max-width:var(--t_container_size);}
}
@media(max-width:767px){
.container{max-width:var(--m_container_size);}
.exam_qustions{display: block;}
.qus_nums{margin: 0 0 10px;width: auto;}
.exam_time, body, .qustion_mcqs ul li, .exam_guid p, .exam_guid li{font-size:14px;}
.exam_qustions_btns{margin-top: 50px;display: flex;flex-wrap: wrap;gap: 10px;}
.body_content{padding:30px 0px;}
.exam_title h1{line-height: 1.25em;font-size: 18px;}
.exam_qustions_btns a{font-size:12px;}
.otp_content input{font-size:13px;height:40px;}
.exam_guid h1, .exam_guid h2, .exam_guid h3,
.exam_guid h4, .exam_guid h5, .exam_guid h6{font-size:16px;}
}

/************/
.exam_name_title{
font-size: 30px;
text-align: center;
color: #000;
}
.result_pass_img{
max-width: 70px;
width: 100%;
margin: 20px auto;
display: block;
}
.exam_tag_line{
font-size: 16px;
color: #999;
}
.scor_bx_wrp{
display: flex;
gap: 20px;
margin-top:30px;
}
.scor_bx_wrp > .scor_bx{
flex:1;
}
.scor_bx_inner{
/*background: #f2f2f2;*/
text-align: center;
padding: 15px 40px;
border-radius: 5px;
box-shadow: 0 0 15px 0 rgba(0,0,0,0.03);
border: 1px solid #eee;
}
.scor_bx_inner h4{
font-size: 20px;
font-weight: 400;
color: #000;
margin: 0;
}
.scor_bx_inner .pre_score{
font-size: 40px;
margin: 20px 0;
display: block;
line-height: 0.7em;
color: #18a618;
}
.scor_bx_inner .pre_score span{
font-size: 70%;
}
.scor_bx_inner p{
margin: 0;
font-size: 15px;
border-top: 1px solid #ddd;
padding-top: 10px;
}
.quiz_reviews{
margin: 25px 0 0;
text-align: center;
}
.exam_result_gh{
box-shadow: 0 0 15px 0 rgba(0,0,0,0.03);
padding: 20px 30px;
border-radius: 10px;
border: 1px solid #eee;
}
.body_content.result_page{
padding:20px 0px;
}
.result_icon{
margin:20px 0px;
}
.result_icon.pass i{
color: #22ac22;
}
.result_icon.fail i{
color: #d5320c;
}
.result_icon i{
font-size: 80px;
}
.red_custom{
  font-size: 40px;
margin: 20px 0;
display: block;
line-height: 0.7em;
color: #d5320c;
}

/************/